.searchPage__logo{
    object-fit: contain;
    height:50px;
    margin-right: 50px;
}
.searchPage__header
{
    display: flex;
    position: sticky;
    top:0;
    background-color: white;
    z-index: 100;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgray;
}
.searchPage__headerBody> .search > .search__input
{
 margin-top: 0;
 width: unset;
 margin: unset;
 max-width: unset;
}
.searchPage__options
{
    display: flex;
    align-items: center;
    color: grey;
}
.searchPage__optionsRight,
.searchPage__optionsLeft
{
     display: flex;
}
 .searchPage__options a,
.searchPage__optionsRight a
{
   text-decoration: none;
   color:gray;
}
.searchPage__optionsRight
{
    margin-left: 80px;
}
.searchPage__option{
    display: flex;
    align-items: center;
    margin-right: 20px;
}
.searchPage__option a{
   
    margin-right: 5px;
}
.searchPage__result
{
    margin: 40px 0;
}

.searchPage__resultCount
{
    color: rgb(112, 117, 122);
    font-size: 14px;

}
a{
    color: #0B0080;
}
.searchPage__results
{
    max-width: 650px;
    margin-top: 20px;
    margin-left: 240px;
    margin-bottom: 100px;

}
.searchPage__resultTitle
{
    text-decoration: none;
    margin-top: 0;
} 
.searchPage__resultTitle:hover {
  text-decoration: underline;
}
.searchPage__resultTitle >h2 {
    font-weight: 500;
}
.searchPage__resultImage
{
    object-fit: contain;
    height: 20px;
    width: 20px;
    margin-right: 10px;
    border-radius: 10px;
}
